export const isbn13 = (str: string | Array<any> | any) => {
    let count = 10;
        let splitedStr= str.split("");
        splitedStr[splitedStr.length-1] === "X" ? splitedStr[splitedStr.length-1] =  10 :null;
        let convertedstr = splitedStr.map((el:any) => el = parseInt(el))
        
            convertedstr = convertedstr.map((el:any)=> el * count--).reduce((a:any,b:any)=>a + b) % 11 === 0
        return convertedstr
  };
  

  console.log(isbn13("0330301624"))